define('xtalus/routes/me', ['exports', 'ember'], function (exports, Ember) {

    'use strict';

    var MeRoute = Ember['default'].Route.extend({

        beforeModel: function beforeModel() {
            //if(this.controller) this.controller.init();
            if (!$ISIS.getCookie('auth')) {
                this.transitionTo('login');
            }
        },

        model: function model() {
            return this.modelFor('application');
        },

        setupController: function setupController(controller, model) {
            controller.set('activePerson', model);
        },

        actions: {
            logout: function logout() {
                $ISIS.auth.logout();
                this.refresh();
            },

            updatePerson: function updatePerson() {
                var activePerson = this.controller.get('activePerson');
                activePerson.updatePerson.invoke({
                    firstName: 'Edgar',
                    lastName: 'Ravenhorst',
                    dateOfBirth: '1991-02-20'
                }).then(function (result) {
                    console.log(result);
                });
            } }
    });

    exports['default'] = MeRoute;

});